import React, { useState } from 'react'
function App() {
  const [diet, setDiet] = useState('')
  const [electricity, setElectricity] = useState('')
  const [fueloil, setFuelOil] = useState('')
  const [propane, setPropane] = useState('')
  const [waste, setWaste] = useState('')
  const [car, setCar] = useState('')
  const [flight, setFlight] = useState('')
  const [emission, setEmission] = useState(0)
  const handleSubmit = (e) => {
    e.preventDefault();
    const values = [diet, electricity, fueloil, propane, waste, car, flight]
    var sum = 0
    for (var i = 0; i < values.length; i++){
      sum += parseFloat(values[i])
    }
    setEmission(sum)
    window.scrollTo(0,document.body.scrollHeight);
  }

  return(
      <div className="app">
        <div className="main">
          <div className="title">
            <p>Carbon Footprint Calculator</p>
          </div>
          <div className="slogan">
            <b>Understand your carbon emission</b>
          </div>
          <form className="input" onSubmit={handleSubmit}>
            <div className="input-box">
              <div className="diet">
                <p className="questions">
                  How would you categorize your diet?
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-11">
                    <input type="radio" name="diet" value="3.3" id="option-11" onChange={(e) => setDiet(e.target.value)}/>
                      Meat Lover
                    </label>
                    <label htmlFor="option-12">
                      <input type="radio" name="diet" value="2.5" id="option-12" onChange={(e) => setDiet(e.target.value)}/>
                      Average
                    </label>
                    <label htmlFor="option-13">
                      <input type="radio" name="diet" value="1.9" id="option-13" onChange={(e) => setDiet(e.target.value)}/>
                      No Beef
                    </label>
                    <label htmlFor="option-14">
                      <input type="radio" name="diet" value="1.7" id="option-14" onChange={(e) => setDiet(e.target.value)}/>
                      Vegetarian
                    </label>
                    <label htmlFor="option-15">
                      <input type="radio" name="diet" value="1.5" id="option-15" onChange={(e) => setDiet(e.target.value)}/>
                      Vegan
                    </label>
                  </div>
                </div>
              </div>
              <div className="electrcity">
                <p className="questions">
                  How would you categorize your electricity use?
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-21">
                      <input type="radio" name="electricity" value="2" id="option-21" onChange={(e) => setElectricity(e.target.value)}/>
                      Below Average (294 kWh/month)
                    </label>
                    <label htmlFor="option-22">
                      <input type="radio" name="electricity" value="2.5" id="option-22" onChange={(e) => setElectricity(e.target.value)}/>
                      Average (367 kWh/month)
                    </label>
                    <label htmlFor="option-23">
                      <input type="radio" name="electricity" value="3" id="option-23" onChange={(e) => setElectricity(e.target.value)}/>
                      Above Average (440 kWh/month)
                    </label>
                  </div>
                </div>
              </div>
              <div className="fuel-oil">
                <p className="questions">
                  How would you categorize your use of fuel oil (heat your home and car)?
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-31">
                      <input type="radio" name="fuel-oil" value="0" id="option-31" onChange={(e) => setFuelOil(e.target.value)}/>
                      Zero
                    </label>
                    <label htmlFor="option-32">
                      <input type="radio" name="fuel-oil" value="1.8" id="option-32" onChange={(e) => setFuelOil(e.target.value)}/>
                      Below Average (38 gallons/month)
                    </label>
                    <label htmlFor="option-33">
                      <input type="radio" name="fuel-oil" value="2.2" id="option-33" onChange={(e) => setFuelOil(e.target.value)}/>
                      Average (46 gallons/month)
                    </label>
                    <label htmlFor="option-34">
                      <input type="radio" name="fuel-oil" value="2.6" id="option-34" onChange={(e) => setFuelOil(e.target.value)}/>
                      Above Average (54 gallons/month)
                    </label>
                  </div>
                </div>
              </div>
              <div className="propane">
                <p className="questions">
                  How would you categorize your use of propane (water heating, cooking, refrigerating, clothes drying)?
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-41">
                      <input type="radio" name="propane" value="0" id="option-41" onChange={(e) => setPropane(e.target.value)}/>
                      Zero
                    </label>
                    <label htmlFor="option-42">
                      <input type="radio" name="propane" value="0.5" id="option-42" onChange={(e) => setPropane(e.target.value)}/>
                      Below Average (19.5 gallons/month)
                    </label>
                    <label htmlFor="option-43">
                      <input type="radio" name="propane" value="1" id="option-43" onChange={(e) => setPropane(e.target.value)}/>
                      Average (39 gallons/month)
                    </label>
                    <label htmlFor="option-44">
                      <input type="radio" name="propane" value="1.5" id="option-44" onChange={(e) => setPropane(e.target.value)}/>
                      Above Average (58.5 gallons/month)
                    </label>
                  </div>
                </div>
              </div>
              <div className="waste">
                <p className="questions">
                  How would you categorize your waste (liquid, solid, organic, recyclable, hazardou)?
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-51">
                      <input type="radio" name="waste" value="0.314" id="option-51" onChange={(e) => setWaste(e.target.value)}/>
                      I don't recycle
                    </label>
                    <label htmlFor="option-52">
                      <input type="radio" name="waste" value="0.25" id="option-52" onChange={(e) => setWaste(e.target.value)}/>
                      I recycle half of my recyclable waste, including metal, plastic, glass, and paper
                    </label>
                    <label htmlFor="option-53">
                      <input type="radio" name="waste" value="0.182" id="option-53" onChange={(e) => setWaste(e.target.value)}/>
                      I recycle all of my recyclable waste, including metal, plastic, glass, and paper
                    </label>
                  </div>
                </div>
              </div>
              <div className="car">
                <p className="questions">
                  How often do you drive your car?
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-61">
                      <input type="radio" name="car" value="0" id="option-61" onChange={(e) => setCar(e.target.value)}/>
                      I don't drive a car
                    </label>
                    <label htmlFor="option-62">
                      <input type="radio" name="car" value="1.43" id="option-62" onChange={(e) => setCar(e.target.value)}/>
                      I sometimes drive a car 
                    </label>
                    <label htmlFor="option-63">
                      <input type="radio" name="car" value="2.38" id="option-63" onChange={(e) => setCar(e.target.value)}/>
                      Average
                    </label>
                    <label htmlFor="option-64">
                      <input type="radio" name="car" value="3.57" id="option-64" onChange={(e) => setCar(e.target.value)}/>
                      I drive a car all the time
                    </label>
                  </div>
                </div>
              </div>
              <div className="flight">
                <p className="questions">
                  What's your flight distance for one year? (Average: 550 miles)
                </p>
                <div className="answers">
                  <div className="block" id="block-11">
                    <label htmlFor="option-71">
                      <input type="radio" name="flight" value="0" id="option-71" onChange={(e) => setFlight(e.target.value)}/>
                      0
                    </label>
                    <label htmlFor="option-72">
                      <input type="radio" name="flight" value="77" id="option-72" onChange={(e) => setFlight(e.target.value)}/>
                      550 miles
                    </label>
                    <label htmlFor="option-73">
                      <input type="radio" name="flight" value="210" id="option-73" onChange={(e) => setFlight(e.target.value)}/>
                      1500 miles
                    </label>
                    <label htmlFor="option-74">
                      <input type="radio" name="flight" value="420" id="option-74" onChange={(e) => setFlight(e.target.value)}/>
                      3000 miles
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div className="btn">
              <button type="submit">Calculate</button>
            </div>
          </form>
          <div className="result">
            <div className="result-box">
              Your annual emission is {emission} metric tons (or {(emission * 2204.62).toFixed(2)} lbs) of CO2
              <br />
              <br />
              The average annual emission per person is {87.83} metric tons (or {(87.83 * 2204.62).toFixed(2)} lbs) of CO2
            </div>
          </div>
        </div>
      </div>
  );
}

export default App;
